<template>
  <mu-container>
  	
			
		<mu-form ref="missionFrom" :model="missionFrom" class="mu-demo-form" >
			<mu-form-item label="任务名称" help-text="这有助于对其管理" prop="mission" :rules="misRules">
				<mu-text-field v-model="missionFrom.mission" prop="mission"></mu-text-field>
			</mu-form-item>
			<mu-form-item label="输入http报文" prop="http" :rules="httpRules">
        <mu-text-field type="text" prop="http" v-model="missionFrom.http" multi-line :rows="3" :rows-max="6"></mu-text-field>
			</mu-form-item>
			
			
		</mu-form >
		<mu-button color='primary'  @click="submit()"> 提交<mu-icon value="check_circle" right></mu-icon></mu-button>
		<br />

	</mu-container>  
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
			missionFrom:{
				mission:'',
				http:''
			},
			misRules: [
				{ validate: (val) => !!val, message: '必须填写任务名称'}
			],
			httpRules: [
				{ validate: (val) => val.length >= 6, message: '必须填写http报文'}
			],
    }
  },
	methods:{
		check:function(){
			this.$refs.missionFrom.validate()			
			if(this.missionFrom.mission.length >= 1 && this.missionFrom.http.length >=6 )return true
			else return false
			
		},
		submit:function(){
			if(this.check()){
				console.log('tg')
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.hello{
	height: 100px;
	background-color: peachpuff;
}
</style>


