<template>
  <div class="wrap">
	<mu-container>
	  <mu-paper class="demo-paper" :z-depth="5">
  <mu-tabs :value.sync="active" inverse color="primary" indicator-color='primary' text-color="rgba(0, 0, 0, .54)"  center>
    <mu-tab> <mu-icon value="face"></mu-icon>个人中心</mu-tab>
    <mu-tab> <mu-icon value="create"></mu-icon>添加任务</mu-tab>
    <mu-tab> <mu-icon value="settings"></mu-icon>任务管理</mu-tab>
  </mu-tabs>
	<div class="demo-text" v-if="active === 0">
    <profile></profile>
  </div>
	<div class="demo-text" v-if="active === 1">
		<add></add>
	</div>
	<div class="demo-text" v-if="active === 2">
		<setting></setting>
	</div>
		</mu-paper>
  </mu-container>  
  </div>
</template>

<script>
import profile from './user/Profile.vue'
import add from './user/add.vue'
import setting from './user/setting.vue'
export default {
  name: 'User',
	components: { add,setting,profile },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
			active: 0,
			items:[
				'aaa',
				'bbb',
				'ccc'
			]
    }
  }
}
</script>


<style scoped>
div.list{
	background-color: #FFFFFF;
}
div.demo-text{
	max-height: calc(99vh - 131px);
	overflow-y: scroll;
}
</style>


