<template>
	
  <mu-container>
	<mu-card>
		<mu-card-header title="使用教程" sub-title="基于安卓平台">

		</mu-card-header>
  <div class="demo-vsteper-container">
    <mu-stepper :active-step="vactiveStep" orientation="vertical">
      <mu-step>
        <mu-step-label>
          1
        </mu-step-label>
        <mu-step-content>
          <p>
            xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
          </p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">下一步</mu-button>
        </mu-step-content>
      </mu-step>
      <mu-step>
				
        <mu-step-label>
          2
        </mu-step-label>
        <mu-step-content>
          <p>
            xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
          </p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">下一步</mu-button>
          <mu-button flat class="demo-step-button" @click="vhandlePrev">上一步</mu-button>
        </mu-step-content>
      </mu-step>
			
      <mu-step>
        <mu-step-label>
          3
        </mu-step-label>
        <mu-step-content>
          <p>
            333333333333333333333333333333333333333333333333
          </p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">完成</mu-button>
          <mu-button flat class="demo-step-button" @click="vhandlePrev">上一步</mu-button>
        </mu-step-content>
      </mu-step>
    </mu-stepper>
    <p v-if="vfinished">
      完成啦!<a href="javascript:;" @click="vreset">点这里</a>再看一遍
    </p>
  </div>
	</mu-card>
</mu-container>

</template>

<script>
export default {
  name: 'Guide',
  data () {
    return {
      vactiveStep: 0
    }
  },
	computed: {
    vfinished () {
      return this.vactiveStep > 2;
    }
  },
  methods: {
    vhandleNext () {
      this.vactiveStep++;
    },
    vhandlePrev () {
      this.vactiveStep--;
    },
    vreset () {
      this.vactiveStep = 0;
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
mu-container{
	background-color: rgba(0,0,0,0);
}
</style>


