<template>
  <mu-container>
		
		 
			 <p style="margin: 5px;color: #0d47a1;">已创建的任务数：</p>
		 
		 <center>
		 	<div id='board' class="board">0</div>
		 </center>
  	
	
		<button @click="show()"></button>
		
		
		<div class="list">
		</div>
      <mu-list>

          <mu-list-item v-for='li in list'>	
            <mu-list-item-title>{{li}}</mu-list-item-title>
						<mu-list-item-action>
							
							<mu-icon value="delete" @click='del(li)'></mu-icon>
						</mu-list-item-action>
          </mu-list-item>
				
      </mu-list>
			
			
			
			<div v-show="showchange">
				<mu-divider></mu-divider>
				<p style="margin: 5px;color: #0d47a1;">垃圾桶：</p>
				<mu-list>
				
									<mu-list-item v-for='i in changelist'>	
										<mu-list-item-title>{{i}}</mu-list-item-title>
										<mu-list-item-action>
											<mu-tooltip content="取消" >
											<mu-icon value="undo" @click='cancel(i)'></mu-icon></mu-tooltip>
										</mu-list-item-action>
									</mu-list-item>
								
				</mu-list>
				<mu-button flat> <mu-icon value="check_circle" right></mu-icon>提交</mu-button>
			</div>
   
		
				
			
		
	</mu-container>  
</template>

<script>
window.btime
window.bn
window.showlistnum = function(){
	if(window.bn < window.n)
	{
			 window.bn++
			document.getElementById('board').innerHTML=window.bn
			if(window.bn == window.n){
				clearInterval(window.btime)

		}
	}
}
export default {
  name: 'Home',
  data () {
    return {
		number:0,
		current: 1,
     list:[
			 'aaa',
			 'bbb',
			 'ccc',
			 'ddd'
		 ],
		 changelist:[
			 
		 ],
		 refreshing: false,
     loading: false,
		 
    }
  },
	computed:{
		showchange(){
			if(this.changelist.length >=1 )
				return true
			else return false
		}
	},
	methods:{
		show:function(){
			window.bn=0
			window.n = this.list.length
			window.btime = setInterval('window.showlistnum()',100)
		},
		cancel:function(i){
			let index
			index = this.changelist.indexOf(i)
			this.list.push(this.changelist[index])
			this.changelist.splice(index,1)
		},
		del:function(i){
			let index
			index = this.list.indexOf(i)
			console.log(this.list[index])
			this.changelist.push(this.list[index])
			this.list.splice(index,1)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	div.board{
		margin: 10px;
		text-align: center;
		font-size:5rem;
		padding: 1rem 0;
		width: 20rem;
		max-width: 80%;
		color: #FFFFFF;
		background-color: #42B983;
	}
	div.list{
		width: 85%;
	}

</style>


