import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import User from '@/components/User'
import Guide from '@/components/Guide'
Vue.use(Router)

const router=new Router({
	//mode:'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
		{
			path: '/Guide',
			name: 'guide',
			component: Guide
		},
		{
			path: '/user',
			name: 'user',
			component: User
		},
  ]
})
 //router
router.beforeEach((to,from,next)=>{
	//alert("404")Register
	if(localStorage.haveLogin){//yes or no
		if (localStorage.haveLogin == 'yes')next()
		else{
			if(to.path == '/' || to.path == '/Guide' || to.path == '/user'  )
				next()
			else 
				next('/')
		}
	}
	else{
		window.localStorage.setItem('haveLogin','no');
	}
})
export default router;


