<template>
  <div >
		<transition name="open-fade" >
			<section class="img" v-show="showfirst" >
					<div class="intro_wrap" style="top:10vh">
						
						<p class="intro_tit" style="font-family: fantasy;">
							Sign in</p>
						<p class="intro_text">
							无论你在哪里<br>
							Sign 让你从此解放双手<br>
							帮助你完成所有的签到任务</p>	
				
					</div>
			
			
			
			</section>
		</transition>
		<section class="img" style="background-image: url(../../static/img/1.jpg);">IMG2</section>
		<section class="img" style="background-image: url(../../static/img/2.jpg);">IMG3</section>
   <!-- <section class="text">
			<p class="my-tit">建议使用</p>
			</section> -->
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      showfirst: false
    }
  },
	mounted(){
		this.showfirst = true
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	section{
		
		overflow: hidden;
	}
section.img {
	height: 100vh;
	
  color: #FFFFFF;
	margin: 0;
	text-align: center;
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
}
section.text{
	color: #000000;
	margin: 0;
	background-color: #FFFFFF;
	
}
p.my-tit{
	font-weight: bold;
	margin: 0px;
}
div.intro_wrap{
	width: 50%;
	
}
p.intro_tit{
	font-weight: 10;

	font-size: calc(20vh);
	margin: 0px;
}
p.intro_text{
	margin: 0px;
}
transition{
	margin: 0px;
}
.open-fade-enter-active {
  transition: all 5s cubic-bezier(0,1.03,.28,.98);
}
.open-fade-leave-active {
  transition: all 2s cubic-bezier(.55,.07,1,-0.05);
}
.open-fade-enter, .open-fade-leave-to{
/*  transform: translateX(20px); */
  opacity: 0;
}
</style>



