<template>
  <mu-container>
  	
			
		<div class="idcard">
			<h3>用户名:{{name}}</h3>
			<p>Email：{{email}}</p>
		</div>
		
		
	</mu-container>  
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
    
			
		}
  },
	mounted(){
		
	},
	computed:{
		name:function(){
			if(localStorage.haveLogin == 'no') return '游客'
		},
		email:function(){
			if(localStorage.haveLogin == 'no') return 'xxx@email.com'
		}
	}
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.idcard{
	padding-top: 20px;
	
}
</style>


